.page {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: #383448;
    margin: 0 1rem;
    vertical-align: top;
}
.page:first-of-type {
    margin-left: 0;
    transition: margin-left 1s ease-in-out 0.5s;
}
.page:last-of-type {
    margin-right: 0;
}

